e>\r\n\r\n                <StreamBarcodeReader\r\n                    v-if=\"dialog\"\r\n                    @decode=\"onDecode\"\r\n                    @loaded=\"onLoaded\"\r\n                ></StreamBarcodeReader>\r\n                <div class=\"respuesta-codigo\">\r\n                   \r\n\r\n                    <h3> {{ scanner_res }}</h3>\r\n\r\n                    <v-alert v-if=\"error\" text  type=\"error\" icon=\"mdi-cloud-alert\">\r\n                        {{ text_error }}\r\n                    </v-alert>\r\n                </div>\r\n            </v-card-text>\r\n\r\n            <v-divider></v-divider>\r\n\r\n            <v-card-actions>\r\n                <v-spacer></v-spacer>\r\n                <v-btn color=\"red\" text @click=\"dialog = false\">\r\n                    <v-icon left>mdi-close</v-icon>\r\n                    Cerrar\r\n                </v-btn>\r\n            </v-card-actions>\r\n        </v-card>\r\n    </v-dialog>\r\n</template>\r\n<script>\r\nimport { StreamBarcodeReader } from \"vue-barcode-reader\";\r\n\r\nexport default {\r\n    components: {\r\n        StreamBarcodeReader,\r\n    },\r\n    data: () => ({\r\n        dialog: false,\r\n        loading_camera: true,\r\n        scanner_res: \"\",\r\n        loading: false,\r\n        error: false,\r\n        text_error: \"\",\r\n    }),\r\n    methods: {\r\n        async onDecode(codigo) {\r\n            this.error = false;\r\n            this.text_error = '';\r\n\r\n            this.scanner_res = codigo;\r\n            this.loading = true;\r\n            let res = await axios.get(\"/autocomplete/bienes/\" + codigo);\r\n\r\n            if (res.data.estado) {\r\n                let item = res.data.datos[0];\r\n                item.registrado = item.registrado == 1 ? true : false;\r\n                this.$emit(\"setData\", item);\r\n                this.dialog = false;\r\n            } else {\r\n                this.error = true;\r\n                this.text_error = res.data.mensaje;\r\n            }\r\n\r\n            this.loading = false;\r\n        },\r\n\r\n        onLoaded() {\r\n            this.loading_camera = false;\r\n        },\r\n    },\r\n};\r\n</script>\r\n\r\n<style>\r\n.overlay-element {\r\n    clip-path: polygon(\r\n        0% 0%,\r\n        0% 100%,\r\n        10% 100%,\r\n        10% 20%,\r\n        90% 20%,\r\n        90% 80%,\r\n        10% 80%,\r\n        9% 100%,\r\n        100% 100%,\r\n        100% 0%\r\n    ) !important;\r\n}\r\n.respuesta-codigo {\r\n    display: flex;\r\n    flex-wrap: wrap;\r\n    justify-content: center;\r\n    align-items: center;\r\n    padding: 1rem;\r\n}\r\n.respuesta-codigo h3{\r\n    width: 100%;\r\n    text-align: center;\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
